body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.pub-item {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-align-items: flex-start;
            align-items: flex-start;
    -webkit-justify-content: center;
            justify-content: center;
    text-align: left;
	margin-top: 20px;
    margin-left: auto;
    margin-right: auto;
    max-width: 940px;
}

.pub-outer {
    margin-left: 200px;
    margin-right: 200px;
}

.pub-title {
    font-size: 1.5vw;
}

.pub-authors {
    font-size: 1vw;
}

.pub-conference {
    font-style: italic;
}

.pub-links {
    font-size:  1vw;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: row;
            flex-direction: row;
    -webkit-justify-content: center;
            justify-content: center;
}

.pub-a {
    margin-right: 5px;
}

.pub-author-conf {
    font-size: 1vw;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: row;
            flex-direction: row;
    -webkit-align-items: baseline;
            align-items: baseline;
}

.pub-a:link {
    color: rgb(252, 238, 238);
  }
  
/* visited link */
.pub-a:visited {
    color: rgb(252, 238, 238);
}
  
/* mouse over link */
.pub-a:hover {
    color: #C96567;
}
  
/* selected link */
.pub-a:active {
  color: black;
}

@media all and (max-width: 900px) {
    .pub-outer {
        margin-left: 20px;
        margin-right: 20px;
    }

    .pub-item {
        max-width: 100%;
    }

    .pub-links {
        font-size:  2.5vw;
    }

    .pub-author-conf {
        font-size: 2.5vw;
    }

    .pub-title {
        font-size: 4vw;
    }
}

@media all and (min-width: 1800px) {
	.pub-title {
	    font-size: 25px;
	}
	
	.pub-authors {
	    font-size: 17px;
	}
	
	.pub-links {
	    font-size:  17px;
	}

    .pub-author-conf {
        font-size: 17px;
    }
}

.Self-Block {
    padding: 10px;
    margin-bottom: 20px;
    margin-top: 0px;
    margin-left: auto;
    margin-right: auto;
    max-width: 940px;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-wrap: wrap;
            flex-wrap: wrap;
    text-align: left;
    font-size: 1.5vw;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-content: center;
            align-content: center;
}

.welcome-text {
    text-align: center;
}

.Intro {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-justify-content: center;
            justify-content: center;
    padding: 10px;
    -webkit-align-items: center;
            align-items: center;
}

.divider {
    background-color: #C96567;
    border: 3px;
    height: 1px;
    width: 100%;
}

.face-img {
	float: right;
    border-radius: 5%;
    max-width:50%;
    border:1.0px solid #C96567;
}

.face {
	-webkit-flex-direction: row;
	        flex-direction: row;
	-webkit-justify-content: center;
	        justify-content: center;
	display: -webkit-flex;
	display: flex;
    -webkit-flex-grow: 1;
            flex-grow: 1;
    margin-top: 15px;
}

.biobox {
	float: left;
}

.face-split-img {
	padding: 10px;
	margin: auto;
	width: 40%;
}

.face-split-bio {
	padding: 10px;
	margin: auto;
	width: 60%;
	height: 20%;
}

.biohead1 {
	margin: 5px;
	font-size: 2.2vw;
}

.biohead2{
	margin: 5px;
	font-size: 1.5vw;
}

.biotext {
	margin: 5px;
	font-size: 1.2vw;
}

.biotext1 {
	margin: 5px;
	font-size: 1.4vw;
}

.text-a:link {
    color: #C96567;
  }
  
/* visited link */
.text-a:visited {
    color: #C96567;
}

li {
	padding: 6px;
}
  
/* mouse over link */
.text-a:hover {
    color: rgb(252, 238, 238);
}
  
/* selected link */
.text-a:active {
  color: black;
}

@media all and (max-width: 900px) {

    .Self-Block{
        -webkit-align-items: center;
                align-items: center;
        -webkit-justify-content: center;
                justify-content: center;
        max-width: 100%;
        margin-left: 20px;
        margin-right: 20px;
		font-size: 3vw;
    }

	.biohead1 {
		font-size: 4vw;
	}
	.biohead2{
		font-size: 2.5vw;
	}
	.biotext {
		font-size: 2vw;
	}
	.biotext1 {
		font-size: 2vw;
	}


    .face-text{
        max-width: 100%;
    }

	.face-split-bio {
		-webkit-justify-content: left;
		        justify-content: left;
	}


    .face-img{
        max-width: 80%;
    }
}


@media all and (min-width: 1800px) {
	.Self-Block{
		font-size: 20px;
	}
}

