.header {
  background-color: #C96567;
  min-height: 5vh;
  padding: 7px;
  color: #314455;
}

.header-text {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: row;
          flex-direction: row;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: space-evenly;
          justify-content: space-evenly;
  font-size: calc(10px + 2vmin);
  margin-left: auto;
  margin-right: auto;
  max-width: 2000px;
}


.header-name {
    margin: 10px;
    padding-left: 10px;
    font-size: 2vw;
    font-family: 'Montserrat', sans-serif;
}

.header-link-divider {
    margin: 5px;
}

.header-links {
    font-size: 1.2vw;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: row;
            flex-direction: row;
    -webkit-align-items: center;
            align-items: center;
    margin: 3px;
}

.header-link {
    -webkit-flex-grow: 1;
            flex-grow: 1;
    padding: 5px;
}

.head-a:link {
	text-decoration: none;
    color: #314455;
  }
  
/* visited link */
.head-a:visited {
  color: #314455;
}
  
/* mouse over link */
.head-a:hover {
  color: rgb(252, 238, 238);
}
  
/* selected link */
