  color: black;
}

@media all and (max-width: 900px) {
  .header{
    -webkit-align-items: center;
            align-items: center;
    -webkit-justify-content: space-evenly;
            justify-content: space-evenly;
  }

  .header-name {
    font-size: 3vw;
  }

  .header-links{
    font-size: 3vw;
    -webkit-justify-content: space-evenly;
            justify-content: space-evenly;
  }
}

