.pics-outers {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}

.pics-intro {
    font-size: 1vw;
	padding: 20px;
}

.divider {
    background-color: #C96567;
    border: 3px;
    height: 1px;
    width: 100%;
}

.pics-container {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
	justify-content: flex-start;
	align-content: flex-start;
    padding: 10px;
    margin-left: 200px;
    margin-right: 200px;
}

.pics-a {
    margin: 10px;
    padding: 10px;
}

.pics-desc {
    color: #314455;
}

.pics-img {
    padding: 5px;
	width: 300px;
	height: 200px;
    border-radius: 5%;
} 

@media all and (max-width: 900px) {
  .pics-container {
      margin-left: 20px;
      margin-right: 20px;
  }

  .pics-img {
	height: 100px;
	width: 150px;
  }

    .pics-intro {
        font-size: 2vw;
    }
}
